import React from 'react';
//
import { cx } from '~/utils/style';

import './Image.css';

export interface ImageProps {
	readonly className?: string;
	readonly src: string;
}

export const Image = ({
	className: customClass,
	src
}: ImageProps) => (
	<div
		className={cx('image', customClass)}
		style={{ backgroundImage: `url(${src})` }}
	/>
);
