import { useIntl } from '@calmdownval/intl-react';
import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
//
import { bem, cx } from '~/utils/style';

import './Navigation.css';

export interface NavigationItem {
	readonly disabled?: boolean;
	readonly intlKey: string;
	readonly route: string;
}

export interface NavigationProps {
	readonly className?: string;
	readonly items: readonly NavigationItem[];
}

export const Navigation = ({ className: customClass, items }: NavigationProps) => {
	const { t } = useIntl();
	const location = useLocation();
	const navigate = useNavigate();
	return (
		<nav className={cx('navigation', customClass)}>
			<ul className='navigation__list'>
				{items.map(item => (
					<li
						key={item.route}
						className={bem('navigation__item', {
							disabled: item.disabled,
							selected: location.pathname === item.route
						})}
					>
						<a
							className='navigation__item-anchor'
							href={item.route}
							onClick={e => {
								e.preventDefault();
								if (!item.disabled) {
									navigate(item.route);
								}
							}}
						>
							{t(item.intlKey)}
						</a>
					</li>
				))}
			</ul>
		</nav>
	);
};
