type Path = string | React.SVGProps<SVGPathElement>;

function createIcon(paths: Path | readonly Path[]) {
	return ({ className: customClass, size = 24 }: IconProps) => (
		<svg
			className={cx('icon', customClass)}
			width={size}
			height={size}
			viewBox='0 0 16 16'
		>
			{Array.isArray(paths) ? paths.map(renderPath) : renderPath(paths as Path)}
		</svg>
	);
}

function renderPath(path: Path) {
	return typeof path === 'string'
		? <path d={path} />
		: createElement('path', path);
}
