import React, { ReactNode } from 'react';
//
import { bem, BEMModifiers, cx } from '~/utils/style';

import './Typography.css';

type TypographyComponents = 'h1' | 'h2' | 'h3' | 'p' | 'span';

export interface TypographyProps {
	readonly children: ReactNode;
	readonly className?: string;
}

/* eslint-disable @typescript-eslint/naming-convention */
export const Typography = {
	PageHeader: createTypography('h1', {
		'page-header': true
	}),
	MainHeader: createTypography('h2', {
		'header': true,
		'main-header': true
	}),
	SubHeader: createTypography('h3', {
		'header': true,
		'sub-header': true
	}),
	Paragraph: createTypography('p', {
		paragraph: true
	}),
	Label: createTypography('span', {
		label: true
	})
};

function createTypography(Component: TypographyComponents, modifiers: BEMModifiers) {
	const baseClass = bem('typography', modifiers);
	return ({
		children,
		className: customClass
	}: TypographyProps) => (
		<Component className={cx(baseClass, customClass)}>
			{children}
		</Component>
	);
}
