import React, { ForwardedRef, forwardRef, MouseEventHandler, ReactNode } from 'react';
import { useNavigate } from 'react-router-dom';
//
import { bem, cx } from '~/utils/style';

import './Button.css';

interface ButtonPropsBase {
	readonly children?: ReactNode;
	readonly className?: string;
	readonly disabled?: boolean;
	readonly icon?: ReactNode;
	readonly onClick?: MouseEventHandler<HTMLButtonElement | HTMLAnchorElement>;
	readonly shrink?: boolean;
	readonly title?: string;
	readonly variant?: 'regular' | 'primary' | 'text';
}

interface TrueButtonProps extends ButtonPropsBase {
	readonly component?: 'button';
}

interface LinkButtonProps extends ButtonPropsBase {
	readonly component: 'a';
	readonly href: string;
	readonly target?: string;
}

export type ButtonProps = TrueButtonProps | LinkButtonProps;

export const Button = forwardRef(({
	children,
	className: customClass,
	component: Component = 'button',
	disabled = false,
	icon,
	shrink = false,
	variant = 'regular',
	...rest
}: ButtonProps, ref: ForwardedRef<HTMLButtonElement | HTMLAnchorElement>) => {
	const isIconOnly = children === undefined && Boolean(icon);
	const cssClass = cx(
		bem('button', {
			[isIconOnly ? 'icon' : variant]: true,
			shrink
		}),
		customClass
	);

	return (
		<Component
			{...rest}
			className={cssClass}
			disabled={disabled}
			ref={ref as any}
		>
			{icon}
			{children && (
				<span className='button__content'>
					{children}
				</span>
			)}
		</Component>
	);
});

export const LinkButton = (props: Omit<LinkButtonProps, 'component'>) => (
	<Button {...props} component='a' />
);

export const RouterLinkButton = ({
	href,
	onClick,
	...rest
}: Omit<LinkButtonProps, 'component' | 'target'>) => {
	const navigate = useNavigate();
	return (
		<Button
			{...rest}
			component='a'
			href={href}
			onClick={e => {
				e.preventDefault();
				navigate(href);
				onClick?.(e);
			}}
		/>
	);
};
