import { RefObject, useMemo } from 'react';

import { useDeferredEffect } from './useDeferredEffect';

interface ListenerInstance<TRef> {
	readonly contentRef: RefObject<TRef>;
	handler?: (e: MouseEvent) => void;
}

export function useClickAwayListener<TRef extends Element = Element>(handler?: (e: MouseEvent) => void, isActive = true) {
	const instance = useMemo<ListenerInstance<TRef>>(() => ({
		contentRef: { current: null }
	}), []);

	// When this hook gets activated as a result of a click event, adding
	// listeners immediately would still catch the same event bubbling up.
	// We defer the effect to avoid this issue.

	instance.handler = handler;
	useDeferredEffect(() => {
		if (!isActive) {
			return undefined;
		}

		const onInterceptClick = (e: MouseEvent) => {
			let element: Element | null = e.target as Element;
			while (element && element !== instance.contentRef.current) {
				element = element.parentElement;
			}

			if (!element) {
				e.preventDefault();
				instance.handler?.(e);
			}
		};

		window.addEventListener('click', onInterceptClick);
		return () => {
			window.removeEventListener('click', onInterceptClick);
		};
	}, [ isActive ]);

	return instance.contentRef;
}
