import { CanvasHTMLAttributes, Component, DetailedHTMLProps, ReactElement, UIEvent } from 'react';

export type CanvasProps = DetailedHTMLProps<CanvasHTMLAttributes<HTMLCanvasElement>, HTMLCanvasElement>;

export abstract class CanvasRenderer<TContext, TProps = {}> extends Component<TProps> {
	protected abstract readonly isAnimated: boolean;

	protected canvas: HTMLCanvasElement | null = null;
	protected gl: TContext | null = null;

	private frameHandle?: number;
	private resizeObserver?: ResizeObserver;

	public shouldComponentUpdate() {
		return false;
	}

	public componentWillUnmount() {
		this.cancelFrame();
		this.resizeObserver?.disconnect();
		this.resizeObserver = undefined;
	}

	public render() {
		return this.onRenderJsx({
			ref: this.onCanvasRef,
			onContextMenu: this.onSideEffect
		});
	}

	protected abstract onAcquireContext(canvas: HTMLCanvasElement): TContext | null;
	protected abstract onResize(width: number, height: number, gl: TContext, canvas: HTMLCanvasElement): void;
	protected abstract onRenderGl(time: number, gl: TContext, canvas: HTMLCanvasElement): void;
	protected abstract onRenderJsx(canvasProps: Partial<CanvasProps>): ReactElement;

	protected scheduleFrame() {
		this.frameHandle ??= requestAnimationFrame(this.onFrame);
	}

	protected cancelFrame() {
		if (this.frameHandle !== undefined) {
			cancelAnimationFrame(this.frameHandle);
		}
	}

	private readonly onCanvasRef = (newCanvas: HTMLCanvasElement | null) => {
		const oldCanvas = this.canvas;
		if (oldCanvas) {
			this.cancelFrame();
			this.resizeObserver?.unobserve(oldCanvas);
			this.gl = null;
		}

		if (newCanvas) {
			this.gl = this.onAcquireContext(newCanvas);
			if (this.gl === null) {
				throw new Error('Could not acquire context.');
			}

			this.resizeObserver ??= new ResizeObserver(this.onResizeObserved);
			this.resizeObserver.observe(newCanvas, { box: 'content-box' });
			this.scheduleFrame();
		}

		this.canvas = newCanvas;
	};

	private readonly onResizeObserved = (entries: ResizeObserverEntry[]) => {
		const rect = entries[0].contentRect;
		this.onResize(rect.width, rect.height, this.gl!, this.canvas!);
		this.scheduleFrame();
	};

	private readonly onFrame = (time: number) => {
		this.onRenderGl(time, this.gl!, this.canvas!);
		this.frameHandle = this.isAnimated
			? requestAnimationFrame(this.onFrame)
			: undefined;
	};

	private readonly onSideEffect = (e: UIEvent) => {
		e.preventDefault();
		return false;
	};
}
