import React, { ReactNode } from 'react';
//
import { bem, cx } from '~/utils/style';

import './Frame.css';

export interface FrameProps {
	readonly children?: ReactNode;
	readonly className?: string;
	readonly elevated?: boolean;
	readonly icon?: ReactNode;
	readonly variant?: 'regular' | 'error';
}

export const Frame = ({
	children,
	className: customClass,
	elevated = false,
	icon,
	variant = 'regular'
}: FrameProps) => (
	<div
		className={bem('frame', {
			elevated,
			[variant]: true
		})}
	>
		{icon && (
			<div className='frame__side'>
				{icon}
			</div>
		)}
		<div className={cx('frame__content', customClass)}>
			{children}
		</div>
	</div>
);
