import { useIntl } from '@calmdownval/intl-react';
import { IWallet } from '@suiet/wallet-kit';
import React from 'react';
//
import { Button } from '~/components/Button';
import { Frame } from '~/components/Frame';
import { Icon } from '~/components/Icon';
import { Typography } from '~/components/Typography';

import './WalletList.css';

const RE_PREFERRED_WALLET = /(^|\s)ethos(\s|$)/i;

export interface WalletListProps {
	readonly wallets: readonly IWallet[];
	readonly onSelect: (wallet: IWallet) => void;
}

export const WalletList = ({ wallets, onSelect }: WalletListProps) => {
	const { t } = useIntl();
	const preferred = [];
	const other = [];

	for (const wallet of wallets) {
		if (RE_PREFERRED_WALLET.test(wallet.name)) {
			preferred.push(wallet);
			continue;
		}

		if (wallet.installed) {
			other.unshift(wallet);
		}
		else {
			other.push(wallet);
		}
	}

	const renderWalletList = (list: readonly IWallet[]) => (
		<ul className='wallet-list__category'>
			{list.map(wallet => (
				<li>
					<Button
						icon={<Icon.External src={wallet.iconUrl} />}
						onClick={() => onSelect(wallet)}
						variant='text'
					>
						{wallet.name}
					</Button>
				</li>
			))}
		</ul>
	);

	return (
		<Frame
			className='wallet-list'
			elevated
		>
			<Typography.SubHeader>
				{t('wallet.preferred')}
			</Typography.SubHeader>
			{renderWalletList(preferred)}
			<Typography.SubHeader>
				{t('wallet.other')}
			</Typography.SubHeader>
			{renderWalletList(other)}
		</Frame>
	);
};
