import { useIntl } from '@calmdownval/intl-react';
import React from 'react';
import { useLocation, useRouteError } from 'react-router-dom';
//
import { AppShell } from '~/components/AppShell';
import { RouterLinkButton } from '~/components/Button';
import { Container } from '~/components/Container';
import { Frame } from '~/components/Frame';
import { Icon } from '~/components/Icon';
import * as Route from '~/routes';

import './ErrorPage.css';

const ERROR_MAPPING: Record<number, string | undefined> = {
	404: 'error.not-found'
};

export const ErrorPage = () => {
	const { t } = useIntl();
	const location = useLocation();
	const error = useRouteError() as { status?: number } | undefined;
	const phrase = ERROR_MAPPING[error?.status ?? 500] ?? 'error.unknown';

	return (
		<AppShell>
			<Container>
				<Frame
					icon={<Icon.Error />}
					variant='error'
				>
					{t(phrase, { path: location.pathname })}
				</Frame>
				<div className='error-page__nav'>
					<RouterLinkButton
						href={Route.Home}
						variant='primary'
					>
						{t('page.error.back-home')}
					</RouterLinkButton>
				</div>
			</Container>
		</AppShell>
	);
};
