import { useIntl } from '@calmdownval/intl-react';
import { IWallet, useWallet } from '@suiet/wallet-kit';
import React, { useReducer } from 'react';
//
import { Button } from '~/components/Button';
import { Icon } from '~/components/Icon';
import { Popup } from '~/components/Popup';
import { useClickAwayListener } from '~/hooks/useClickAwayListener';
import { bem, cx } from '~/utils/style';

import './Wallet.css';
import { WalletInstallPrompt } from './WalletInstallPrompt';
import { WalletList } from './WalletList';

interface WalletState {
	readonly kind: 'idle' | 'connect' | 'disconnect' | 'install';
	readonly wallet?: IWallet;
}

const IDLE: WalletState = { kind: 'idle' };

export interface WalletProps {
	readonly className?: string;
}

export const Wallet = ({ className: customClass }: WalletProps) => {
	const { t } = useIntl();
	const [ state, dispatch ] = useReducer(walletStateReducer, IDLE);
	const reset = () => dispatch(IDLE);

	const wallet = useWallet();
	const { connected } = wallet;
	const buttonRef = useClickAwayListener<HTMLButtonElement>(reset, state.kind === 'disconnect');

	return (
		<div className={cx(bem('wallet', { disconnect: state.kind === 'disconnect' }), customClass)}>
			<Button
				className='wallet__button-main'
				icon={<Icon.Wallet />}
				onClick={() => dispatch({ kind: connected ? 'disconnect' : 'connect' })}
				variant={connected ? 'text' : 'primary'}
				shrink
			>
				{t(connected ? 'wallet.connected' : 'wallet.connect')}
			</Button>
			<Button
				ref={buttonRef}
				className='wallet__button-disconnect'
				icon={<Icon.Error />}
				title={t('wallet.disconnect')}
				onClick={() => {
					void wallet.disconnect().then(reset);
				}}
			/>
			<Popup
				open={state.kind === 'connect'}
				onClose={reset}
				overlay
			>
				{() => (
					<WalletList
						wallets={wallet.configuredWallets}
						onSelect={newWallet => {
							if (newWallet.installed) {
								wallet.select(newWallet.name);
								reset();
							}
							else {
								dispatch({
									kind: 'install',
									wallet: newWallet
								});
							}
						}}
					/>
				)}
			</Popup>
			<Popup
				open={state.kind === 'install'}
				onClose={reset}
				overlay
			>
				{() => (
					<WalletInstallPrompt
						wallet={state.wallet!}
						onRedirected={() => dispatch({ kind: 'connect' })}
					/>
				)}
			</Popup>
		</div>
	);
};

function walletStateReducer(state: WalletState, action: WalletState): WalletState {
	return {
		...state,
		...action
	};
}
