import React, { useMemo } from 'react';
import { createBrowserRouter, createHashRouter, RouterProvider } from 'react-router-dom';
//
import { AppShell } from '~/components/AppShell';
import { ErrorPage } from '~/pages/error';
import { HomePage } from '~/pages/home';
import { MyCollectionPage } from '~/pages/my-collection';
import * as RoutePath from '~/routes';

export const AppRouter = () => {
	const router = useMemo(createRouter, []);
	return <RouterProvider router={router} />;
};

function createRouter() {
	const root = {
		path: RoutePath.Home,
		element: <AppShell />,
		errorElement: <ErrorPage />,
		children: [
			{
				index: true,
				element: <HomePage />
			},
			// {
			// 	path: RoutePath.MintShop,
			// 	element: <MintShopPage />
			// },
			// {
			// 	path: RoutePath.Configurator,
			// 	element: <ConfiguratorPage />
			// },
			{
				path: RoutePath.MyCollection,
				element: <MyCollectionPage />
			}
		]
	};

	return (__ROUTER_USES_HASH__ ? createHashRouter : createBrowserRouter)(
		[ root ],
		{ basename: __ROUTER_BASENAME__ }
	);
}
