import React, { ReactNode, RefObject, useMemo } from 'react';
//
import { useClickAwayListener } from '~/hooks/useClickAwayListener';
import { useDeferredEffect } from '~/hooks/useDeferredEffect';
import { cx } from '~/utils/style';

import './Popup.css';

interface PupupInstance {
	readonly wrapperRef: RefObject<HTMLDivElement>;
	isActuallyOpen: boolean;
}

export interface PopupProps {
	readonly children?: (() => ReactNode) | ReactNode;
	readonly className?: string;
	readonly onClose?: (e: MouseEvent) => void;
	readonly open?: boolean;
	readonly overlay?: boolean;
}

export const Popup = ({
	children,
	className: customClass,
	onClose,
	open: shouldOpen = false,
	overlay = false
}: PopupProps) => {
	const popupContentRef = useClickAwayListener<HTMLDivElement>(onClose, shouldOpen);
	const instance = useMemo<PupupInstance>(() => ({
		wrapperRef: { current: null },
		isActuallyOpen: false
	}), []);

	useDeferredEffect(() => {
		const { current: wrapper } = instance.wrapperRef;
		if (!wrapper) {
			return;
		}

		if (shouldOpen) {
			wrapper.classList.add('popup--open');
			instance.isActuallyOpen = true;
			return;
		}

		wrapper.classList.remove('popup--open');
		wrapper.addEventListener('transitionend', () => {
			instance.isActuallyOpen = false;
		}, { once: true });
	}, [ shouldOpen ]);

	return shouldOpen || instance.isActuallyOpen
		? (
			<div
				className='popup'
				ref={instance.wrapperRef}
			>
				{overlay && <div className='popup__overlay' />}
				<div
					className={cx('popup__content', customClass)}
					ref={popupContentRef}
				>
					{typeof children === 'function' ? children() : children}
				</div>
			</div>
		)
		: null;
};
