import { useIntl } from '@calmdownval/intl-react';
import { IWallet } from '@suiet/wallet-kit';
import React from 'react';
//
import { Button } from '~/components/Button';
import { Frame } from '~/components/Frame';
import { Typography } from '~/components/Typography';

import './WalletInstallPrompt.css';

export interface WalletInstallPromptProps {
	readonly wallet: IWallet;
	readonly onRedirected: () => void;
}

export const WalletInstallPrompt = ({ wallet, onRedirected }: WalletInstallPromptProps) => {
	const { t } = useIntl();
	return (
		<Frame
			className='wallet-install-prompt'
			elevated
		>
			<Typography.SubHeader>
				{t('wallet.install-prompt', { wallet: wallet.name })}
			</Typography.SubHeader>
			<div className='wallet-install-prompt__actions'>
				<Button
					variant='text'
					onClick={onRedirected}
				>
					{t('wallet.back')}
				</Button>
				<Button
					variant='primary'
					onClick={() => {
						window.open(wallet.downloadUrl.browserExtension);
						onRedirected();
					}}
				>
					{t('wallet.install')}
				</Button>
			</div>
		</Frame>
	);
};
