/* eslint-disable array-element-newline, no-multi-spaces */
import React from 'react';
//
import { CanvasProps, CanvasRenderer } from '~/components/CanvasRenderer';

import bubblesFragmentShader from './bubbles.fragment.glsl';
import bubblesVertexShader from './bubbles.vertex.glsl';
import { createProgram } from './utils';

import './BackgroundRenderer.css';

export class BackgroundRenderer extends CanvasRenderer<WebGL2RenderingContext> {
	protected readonly isAnimated = true;

	private renderer: ((time: number) => void) | null = null;

	protected onAcquireContext(canvas: HTMLCanvasElement) {
		const gl = canvas.getContext('webgl2', {
			alpha: false,
			antialias: false,
			depth: false,
			desynchronized: true,
			failIfMajorPerformanceCaveat: true,
			powerPreference: 'high-performance',
			premultipliedAlpha: false,
			preserveDrawingBuffer: false,
			stencil: false
		});

		if (!gl) {
			throw new Error('Could not obtain WebGL rendering context.');
		}

		gl.enable(gl.BLEND);
		gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);

		this.renderer = createRenderer(gl);
		return gl;
	}

	protected onResize(width: number, height: number, gl: WebGL2RenderingContext, canvas: HTMLCanvasElement) {
		canvas.width = width;
		canvas.height = height;
		gl.viewport(0, 0, gl.drawingBufferWidth, gl.drawingBufferHeight);
	}

	protected onRenderGl(time: number): void {
		this.renderer!(time);
	}

	protected onRenderJsx(canvasProps: Partial<CanvasProps>) {
		return (
			<canvas
				className='background-renderer'
				{...canvasProps}
			/>
		);
	}
}

function createRenderer(gl: WebGL2RenderingContext) {
	const program = createProgram(gl, {
		shaders: [
			{
				source: bubblesVertexShader,
				type: gl.VERTEX_SHADER
			},
			{
				source: bubblesFragmentShader,
				type: gl.FRAGMENT_SHADER
			}
		],
		attributes: [ 'aClipPosition' ],
		uniforms: [ 'uResolution', 'uTime' ]
	} as const);

	const vao = gl.createVertexArray();
	gl.bindVertexArray(vao);

	const clipPosGlBuffer = gl.createBuffer();
	const clipPosData = new Int8Array([
		-1, -1,
		 1, -1,
		-1,  1,
		 1,  1
	]);

	gl.bindBuffer(gl.ARRAY_BUFFER, clipPosGlBuffer);
	gl.bufferData(gl.ARRAY_BUFFER, clipPosData, gl.STATIC_READ);

	gl.enableVertexAttribArray(program.attributes.aClipPosition);
	gl.vertexAttribPointer(program.attributes.aClipPosition, 2, gl.BYTE, false, 0, 0);

	return (time: number) => {
		gl.useProgram(program.ref);
		gl.bindVertexArray(vao);

		gl.uniform2fv(program.uniforms.uResolution, [ gl.drawingBufferWidth, gl.drawingBufferHeight ]);
		gl.uniform1f(program.uniforms.uTime, time / 1000);

		gl.drawArrays(gl.TRIANGLE_STRIP, 0, 4);
	};
}
